import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { RouterModule } from '@angular/router';


import { AppComponent } from './app.component';
import { CrosswordViewComponent } from './crossword-view/crossword-view.component';

@NgModule({
  declarations: [
    AppComponent,
    CrosswordViewComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    RouterModule.forRoot([
        { path: '', redirectTo: 'crossword-view', pathMatch: 'full' },
        { path: 'crossword-view', component: CrosswordViewComponent },
        { path: '**', redirectTo: 'crossword-view' }
    ], { useHash: true })
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }



